
import {
  computed,
  defineAsyncComponent,
  defineComponent,
  onMounted,
  reactive,
  ref,
  watch,
} from "vue";
import { gpf } from "@/utils/global-functions";
import { dataPayStatus, dataPayType, dataStatus } from "../data-pemasukan";
import { formatDate } from "@/utils/date-format";
import { btc, convertToCurrency, ctc } from "@/utils/text-format";
import { loadingScreen } from "@/utils/loading-screen";
import type {
  PackageType,
  PaymentType,
  PromoType,
} from "@/services/v1/public/types";
import type { DetailPemasukan } from "@/services/v1/admin/types";

const ModalTemplate = defineAsyncComponent(
  () => import(`@/components/modal-template/Main.vue`)
);

const LabelWithColon = defineAsyncComponent(
  () => import(`@/components/label/WithColon.vue`)
);

const servicesV1Public = async () => await import(`@/services/v1/public/index`);
const servicesV1Admin = async () => await import(`@/services/v1/admin/index`);

export default defineComponent({
  components: {
    ModalTemplate,
    LabelWithColon,
  },
  props: {
    invoiceNumber: {
      type: String,
      default: "",
    },
  },
  setup(props) {
    const modalId = "modal-edit-pemasukan";
    const form = reactive({
      price: "",
      uniqueCode: "",
      paymentMethod: "",
      paymentType: "",
      paymentStatus: "",
      status: "",
      paymentId: 0 as number,
      packageId: 0 as number,
      promoId: 0 as number,
    });

    const listPackage = ref([] as PackageType[]);
    const getListPackage = async () => {
      const { getPackage } = await servicesV1Public();

      loadingScreen("Loading... Memuat Data Paket.").show();

      try {
        const hit = await getPackage();
        console.log("res data package ", hit);

        listPackage.value = hit?.response || [];

        loadingScreen().hide();
      } catch (e: any) {
        const err = e.response;
        console.log("err response ", err);

        gpf.handleEds(e);
        loadingScreen().hide();
      }
    };
    onMounted(() => getListPackage());

    const listPayment = ref([] as PaymentType[]);
    const getListPayment = async () => {
      const { getPayment } = await servicesV1Public();

      loadingScreen("Loading... Memuat Data Payment.").show();

      try {
        const hit = await getPayment();
        console.log("res data payment ", hit);

        listPayment.value = hit?.response || [];

        loadingScreen().hide();
      } catch (e: any) {
        const err = e.response;
        console.log("err response ", err);

        gpf.handleEds(e);
        loadingScreen().hide();
      }
    };
    onMounted(() => getListPayment());

    const listPromo = ref([] as PromoType[]);
    const getListPromo = async (params: any) => {
      const { getPromo } = await servicesV1Public();

      loadingScreen("Loading... Memuat Data Promo.").show();

      try {
        const hit = await getPromo({ params });
        console.log("res data promo ", hit);

        listPromo.value = hit?.response || [];

        loadingScreen().hide();
      } catch (e: any) {
        const err = e.response;
        console.log("err response ", err);

        gpf.handleEds(e);
        loadingScreen().hide();
      }
    };

    const detailDP = ref({} as DetailPemasukan);
    const loading = ref(false as boolean);
    const getDetailTransaksi = async () => {
      const { getTransaksiById } = await servicesV1Admin();

      loading.value = true;

      try {
        const hit = await getTransaksiById(props.invoiceNumber);
        console.log("res data detail transaksi ", hit);

        detailDP.value = hit.response;
        populateDataDP();

        const params = {
          refid: detailDP.value?.refferal?.id,
        };
        await getListPromo(params);

        loading.value = false;
      } catch (e: any) {
        loading.value = false;

        const err = e.response;
        console.log("err response ", err);

        gpf.handleEds(e);
      }
    };

    watch(
      () => props.invoiceNumber,
      (newVal) => {
        console.log("props.invoiceNumber ", newVal);
        if (newVal) getDetailTransaksi();
      }
    );

    const populateDataDP = () => {
      const rs = detailDP.value;

      form.price = ctc(rs.price as string);
      form.uniqueCode = rs.uniqueCode;
      form.paymentMethod = rs.paymentMethod;
      form.paymentType = rs.paymentType;
      form.paymentStatus = rs.paymentStatus;
      form.status = rs.status;

      form.packageId = (rs?.paket?.id || 0) as any;
      form.paymentId = (rs?.payment?.id || 0) as any;
      form.promoId = (rs?.promo?.id || 0) as any;
    };

    // data confirm pemasukan
    const confirm = computed(() => {
      const dd = detailDP.value?.confirm;

      return {
        ...dd,
        accountName: dd?.accountName || "-",
        accountNumber: dd?.accountNumber || "-",
        bankName: dd?.bankName || "-",
        paymentType: dd?.paymentType || "-",
        nominal: convertToCurrency(dd?.nominal as string, "IDR ") || "-",
        status: dd?.status || "-",
        confirmAt:
          formatDate(dd?.confirmAt as string, "DD-MMM-YYYY HH:mm:ss") || "-",
        attachment: dd?.attachment || "-",
      };
    });

    // data member pemasukan
    const member = computed(() => {
      const dd = detailDP.value?.member;

      return {
        ...dd,
        name: dd?.name || "-",
        phone: dd?.phone || "-",
        email: dd?.email || "-",
      };
    });

    // data refferal pemasukan
    const refferal = computed(() => {
      const dd = detailDP.value?.refferal;

      return {
        ...dd,
        name: dd?.name || "-",
        code: dd?.code || "-",
      };
    });

    const ctcDP = (f: string) => {
      const ex: any = form;
      ex[f] = gpf.ctc(ex[f]);
    };

    const changeTS = (v: any, f: any) => {
      const val = v.detail.args[1].key;
      const ex: any = form;
      ex[f] = val;
    };

    const listStatus = dataStatus();
    const listPayType = dataPayType();
    const listPayStatus = dataPayStatus();

    const onCancelClick = () => {
      gpf.hideModal(`#${modalId}`);
    };

    const onUpdateClick = async () => {
      const { updateIncome } = await servicesV1Admin();

      loadingScreen("Loading... Sedang memperbarui data pemasukan.").show();

      try {
        const body = new FormData();

        body.append("harga", btc(form.price));
        body.append("kodeUnik", form?.uniqueCode || "");
        body.append("paymentType", form?.paymentType || "");
        body.append("paymentStatus", form?.paymentStatus || "");
        body.append("status", form?.status || "");
        body.append("packageId", form?.packageId.toString() || "");
        body.append("paymentId", form?.paymentId.toString() || "");
        body.append("promoId", form?.promoId.toString() || "");

        await updateIncome(props.invoiceNumber, body);

        gpf.handleSdsV2("Memperbarui Data Pemasukan Berhasil", "toast");
        gpf.hideModal(`#${modalId}`);

        loadingScreen().hide();
      } catch (e: any) {
        const err = e.response;
        console.log("err response ", err);

        gpf.handleEds(e);
        loadingScreen().hide();
      }
    };

    return {
      modalId,
      form,
      ctcDP,
      changeTS,

      listStatus,
      listPayType,
      listPayStatus,

      formatDate,
      convertToCurrency,

      listPackage,
      listPayment,
      listPromo,

      loading,
      detailDP,
      member,
      refferal,
      confirm,

      onCancelClick,
      onUpdateClick,
    };
  },
});
